 at differents magnetic fields. Futhermore, I measured a HCTS that was discovered only two years ago. The most important result is that I found how the internal magnetism and superconductivity are coexisting with each other. ")]),_c('h6',{staticClass:"q-my-sm text-weight-bolder"},[_vm._v("How")]),_c('p',[_vm._v(" In order to obtain my results, I developed a scanning tunneling microscope from scratch to perform at low temperatures (~150 mK) and high magnetic fields (~15 T). This microscope was invented in 1985 and allows us to obtain topographic images at the atomic level and the electronical properties of the material we are measuring. For example, we can obtain vortex images by measuring the electronic properties locally. I designed and built the microscope and also help in the development of the mechanical apparatus, electronics and computer comunications do the experiments. ")]),_c('p',[_vm._v(" Another very important part in my thesis is the data analysis. With the microscope I can obtain files of several Gb per measurement. So, in order to do faster analysis, I developed a software in Matlab. Using as input the raw data we are able to open, manage and clean it in seconds. Mostof the funcionalities are for image analysis. Some of them are: "),_c('ul',[_c('li',[_vm._v(" 2D-Fast fourier transformations and filterings.")]),_c('p'),_c('li',[_vm._v(" Automatic vortex detection in images. ")]),_c('p'),_c('li',[_vm._v(" Analysis of vortex movemen in movies. ")]),_c('p'),_c('li',[_vm._v(" Fractal analysis. ")]),_c('p'),_c('li',[_vm._v(" Hyperuniform detection in real and reciprocal space. ")]),_c('p')])]),(this.$q.platform.is.mobile)?_c('div',[_c('q-carousel',{attrs:{"control-color":"black","animated":"","arrows":"","navigation":"","height":"250px"},model:{value:(_vm.slide),callback:function ($$v) {_vm.slide=$$v},expression:"slide"}},[_c('q-carousel-slide',{attrs:{"name":1,"img-src":require('./ThesisPresentation.jpg')}}),_c('q-carousel-slide',{attrs:{"name":2,"img-src":